// http.service.spec.ts
import 'zone.js/testing'; // üëà Necesario para tests

import { TestBed } from '@angular/core/testing';
import { HttpClientTestingModule, HttpTestingController } from '@angular/common/http/testing';
import { HttpService } from './http.service';

describe('HttpService', () => {
  let service: HttpService;
  let httpMock: HttpTestingController;

  beforeEach(() => {
    TestBed.configureTestingModule({
      imports: [HttpClientTestingModule],
      providers: [HttpService]
    });

    service = TestBed.inject(HttpService);
    httpMock = TestBed.inject(HttpTestingController);
  });

  afterEach(() => {
    httpMock.verify(); 
  });

  it('deber√≠a hacer una petici√≥n GET correctamente', () => {
    const mockData = { name: 'Samir' };

    service.get<any>('https://api.example.com/user').subscribe(data => {
      expect(data).toEqual(mockData);
    });

    const req = httpMock.expectOne('https://api.example.com/user');
    expect(req.request.method).toBe('GET');
    req.flush(mockData);
  });

  it('deber√≠a manejar errores correctamente', () => {
    service.get<any>('https://api.example.com/error').subscribe({
      next: () => fail('La petici√≥n deber√≠a fallar'),
      error: (error) => {
        expect(error.message).toContain('Ocurri√≥ un error');
      }
    });

    const req = httpMock.expectOne('https://api.example.com/error');
    req.flush('Error', { status: 500, statusText: 'Server Error' });
  });
});